@font-face {
  font-family: 'Montserrat';
  src: url("../fonts/Montserrat-Regular.woff2");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'Montserrat';
  src: url("../fonts/Montserrat-Bold.woff2");
  font-weight: 700;
  font-style: normal;
}

* {
  box-sizing: border-box;
  margin: 0;
}

ul {
  list-style: none;
  padding: 0;
}

body {
  font-family: 'Montserrat', 'Arial', sans-serif;
  font-weight: 700;
  min-width: 375px;
}

.stop-scroll {
  overflow: hidden;
  height: 100%;
}

.container {
  max-width: 1440px;
  min-width: 375px;
  margin: 0 auto;
  padding: 0 30px;
}

a {
  text-decoration: none;
}

.img-responsive {
  max-width: 100%;
  height: auto;
}

.vissualy-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@media screen and (min-width: 768px) {
  .container {
    padding: 0 60px;
  }
}

.header {
  background: linear-gradient(228.57deg, #323264 11.93%, #323264 57.17%, #643E72 87.46%);
  border-radius: 0px 0px 12px 12px;
  margin-bottom: 50px;
}

.header__bg-img {
  background-image: url(../img/home-page/big-banner.svg);
  background-repeat: no-repeat;
  background-position: center center;
  margin-bottom: 30px;
  background-size: cover;
}

.header__wrap {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, auto);
  margin-bottom: 139px;
}

.header__wrap-menu-logo {
  grid-column-start: -3;
  grid-column-end: -2;
  margin: 15px 0;
  margin-left: -15px;
}

.header__wrap-menu-logo-img {
  height: 32px;
}

.header__wrap-icon-menu {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.header__subtitle {
  font-size: 38px;
  line-height: 46px;
  text-align: center;
  color: #FFFFFF;
  margin-bottom: 159px;
}

.header__arrow-down {
  display: flex;
  justify-content: center;
  padding-bottom: 30px;
}

.header__arrow-down-border {
  width: 42px;
  height: 42px;
  border: 2px solid rgba(255, 255, 255, 0.5);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header__arrow-down-border:hover, .header__arrow-down-border:active {
  filter: invert(31%) sepia(100%) saturate(6241%) hue-rotate(331deg) brightness(94%) contrast(99%);
}

.header__shadow {
  position: relative;
}

.header__shadow::after {
  content: '';
  background-color: #CACADB;
  height: 10px;
  border-radius: 0px 0px 12px 12px;
  position: absolute;
  left: 20px;
  right: 20px;
  bottom: -10px;
}

.header__shadow::before {
  content: '';
  background-color: #E7E7F2;
  height: 10px;
  border-radius: 0px 0px 12px 12px;
  position: absolute;
  left: 40px;
  right: 40px;
  bottom: -20px;
}

@media screen and (min-width: 768px) {
  .header {
    margin-bottom: 80px;
  }
  .header__bg-img {
    margin-bottom: 60px;
  }
  .header__wrap {
    display: grid;
    grid-template-columns: repeat(2, auto);
    grid-template-rows: repeat(3, auto);
    margin-bottom: 270px;
    padding-top: 50px;
  }
  .header__wrap-menu-logo {
    margin: 0 0 30px;
  }
  .header__wrap-menu-logo-img {
    height: 60px;
  }
  .header__wrap-icon-menu {
    display: none;
  }
  .header__subtitle {
    margin-bottom: 417px;
  }
}

@media screen and (min-width: 1024px) {
  .header__bg-img {
    background-size: auto;
  }
  .header__wrap {
    margin-bottom: 250px;
  }
  .header__wrap-menu-logo {
    grid-row-start: 1;
    grid-row-end: 4;
    margin: 0;
  }
  .header__subtitle {
    margin-bottom: 290px;
  }
}

@media screen and (min-width: 1440px) {
  .header__wrap {
    margin-bottom: 265px;
  }
  .header__subtitle {
    margin-bottom: 305px;
  }
}

.nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: rgba(245, 245, 250, 0.2);
  margin: 0 -30px;
  padding: 10px 15px;
  grid-column-start: 1;
  grid-column-end: 3;
}

.nav__lang-list {
  display: flex;
}

.nav__lang-list-item {
  padding: 5px 0;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: 1px;
  text-transform: uppercase;
}

.nav__lang-list-link {
  border-radius: 8px;
  padding: 5px 7px;
}

.nav__lang-list-link--active {
  background-color: #F0145A;
  color: #FFFFFF;
}

.nav__lang-list-link--dark {
  color: #FFFFFF;
}

.nav__lang-list-link--dark:hover {
  background-color: rgba(255, 255, 255, 0.2);
}

.nav__lang-list-link--white {
  color: #B3B3BA;
}

.nav__lang-list-link--white:hover {
  background-color: #F5F5FA;
}

.nav__address {
  display: none;
}

.nav__tel {
  line-height: 20px;
  color: #FFFFFF;
}

.nav__facebook {
  height: 24px;
  display: none;
}

@media screen and (min-width: 768px) {
  .nav {
    margin: 0;
    background: none;
    padding: 0 0 30px;
    justify-content: flex-start;
  }
  .nav__address {
    display: flex;
    font-weight: 400;
    line-height: 20px;
    color: #B3B3BA;
    align-items: center;
  }
  .nav__address::after, .nav__address::before, .nav__tel::after {
    content: '';
    width: 6px;
    height: 6px;
    background: #E6E6F0;
    border-radius: 50%;
    margin: 0 6px;
  }
  .nav__tel {
    display: flex;
    align-items: center;
  }
  .nav__facebook {
    display: block;
    filter: brightness(0) saturate(100%) invert(98%) sepia(11%) saturate(147%) hue-rotate(166deg) brightness(117%) contrast(100%);
  }
  .nav__facebook:hover {
    filter: invert(31%) sepia(100%) saturate(6241%) hue-rotate(331deg) brightness(94%) contrast(99%);
  }
}

@media screen and (min-width: 1024px) {
  .nav {
    grid-column-start: 2;
    justify-content: flex-end;
    padding-bottom: 16px;
  }
}

.footer {
  background-color: #F5F5FA;
}

.footer__wrap {
  text-align: center;
  padding: 30px 0;
}

.footer__address {
  margin-bottom: 20px;
}

.footer__address-text {
  font-weight: 400;
  line-height: 20px;
  color: #323264;
  margin-bottom: 10px;
}

.footer__contact {
  margin-bottom: 30px;
}

.footer__contact-facebook {
  margin-bottom: 10px;
}

.footer__contact-tel {
  margin-bottom: 10px;
}

.footer__contact-tel-link {
  color: #46466E;
  line-height: 20px;
}

.footer__law {
  font-weight: 400;
  line-height: 20px;
  color: #B3B3BA;
}

@media screen and (min-width: 768px) {
  .footer__wrap {
    text-align: start;
    display: grid;
    grid-template-columns: repeat(2, auto);
    grid-template-rows: repeat(2, auto);
    grid-row-gap: 20px;
  }
  .footer__contact {
    grid-column-start: -3;
    margin-bottom: 0;
    grid-column-end: -1;
  }
  .footer__law {
    grid-column-start: -2;
    grid-column-end: -1;
    grid-row-start: 1;
    grid-row-end: 2;
  }
  .footer__address, .footer__contact, .footer__law {
    display: flex;
    align-items: center;
  }
  .footer__address {
    margin-bottom: 0;
  }
  .footer__address-text {
    display: flex;
    align-items: center;
    margin-bottom: 0;
    line-height: inherit;
  }
  .footer__address-text::after {
    content: '';
    width: 6px;
    height: 6px;
    background: #B3B3BA;
    border-radius: 50%;
    margin: 0 10px;
  }
  .footer__address-link {
    margin-bottom: 1px;
  }
  .footer__contact-facebook {
    margin-bottom: 0;
  }
  .footer__contact-tel {
    display: flex;
    align-items: center;
    margin-bottom: 0;
  }
  .footer__contact-tel-link {
    line-height: inherit;
  }
  .footer__contact-tel::after, .footer__contact-tel::before {
    content: '';
    width: 6px;
    height: 6px;
    background: #B3B3BA;
    border-radius: 50%;
    margin: 0 10px;
  }
  .footer__contact-email {
    margin-bottom: 1px;
  }
  .footer__law {
    line-height: inherit;
    justify-content: flex-end;
  }
}

.header__menu {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #707093;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  opacity: 0;
  visibility: hidden;
  z-index: 2;
  transition: all 0.3s ease;
}

.header__menu.show {
  transition: opacity 0.3s ease;
  opacity: 1;
  visibility: visible;
}

.header__menu-list {
  background-color: #FFFFFF;
  width: 100%;
  border-radius: 12px 12px 0px 0px;
  margin: 0 8px;
  padding: 0 15px;
  display: flex;
  flex-direction: column;
  transform: translateY(100%);
  z-index: 3;
  transition: all 0.3s ease;
}

.header__menu-list.show {
  transition: transform 0.3s ease;
  transform: translateY(0%);
}

.header__menu-list-item {
  background-color: #F5F5FA;
  width: 100%;
  border-radius: 12px;
  text-align: center;
  margin-bottom: 15px;
  padding: 7px 0;
}

.header__menu-list-item:last-child {
  margin-bottom: 12px;
}

.header__menu-list-item:active {
  background-color: #E6E6F0;
}

.header__menu-list-link {
  font-size: 24px;
  line-height: 29px;
  color: #24A3FF;
}

@media screen and (min-width: 768px) {
  .header__menu {
    position: static;
    opacity: 1;
    visibility: visible;
    background: none;
    grid-column-start: -3;
    grid-column-end: -1;
  }
  .header__menu-list {
    transform: translateY(0%);
    flex-direction: inherit;
    background: none;
    margin: 0;
    padding: 0;
  }
  .header__menu-list-item {
    background: none;
    padding: 0;
    margin-bottom: 0;
    width: auto;
    margin-right: 15px;
  }
  .header__menu-list-item:last-child {
    margin-bottom: 0;
  }
  .header__menu-list-link {
    font-size: 16px;
    line-height: 20px;
    color: #FFFFFF;
  }
}

@media screen and (min-width: 1024px) {
  .header__menu {
    grid-column-start: 2;
    grid-column-end: 3;
  }
  .header__menu-list {
    justify-content: flex-end;
  }
  .header__menu-list-item:last-child {
    margin-right: 0;
  }
}

.icon__menu {
  width: 32px;
  height: 32px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-right: -15px;
}

.icon__menu:active {
  background-color: #E6E6F0;
  border-radius: 8px;
}

.icon__menu::after, .icon__menu::before {
  content: "";
  background-color: #FFFFFF;
  height: 2px;
  width: 18px;
}

.icon__menu-span {
  background-color: #FFFFFF;
  height: 2px;
  width: 18px;
  margin: 5px 0;
}

.close-icon__menu {
  display: flex;
  justify-content: center;
  position: relative;
  display: flex;
  align-items: center;
  width: 32px;
  height: 32px;
  align-self: flex-end;
  margin: 12px 0 20px;
}

.close-icon__menu::after, .close-icon__menu::before {
  content: "";
  background-color: #EC6F5D;
  height: 2px;
  width: 75%;
  position: absolute;
}

.close-icon__menu::before {
  transform: rotate(45deg);
}

.close-icon__menu::after {
  transform: rotate(-45deg);
}

.close-icon__menu:hover, .close-icon__menu:active {
  background: #E6E6F0;
  border-radius: 8px;
}

@media screen and (min-width: 768px) {
  .close-icon__menu {
    display: none;
  }
}

.solution__box {
  margin-bottom: 30px;
  position: relative;
}

.solution__box-subject {
  display: flex;
  overflow: auto;
  -ms-overflow-style: none;
  margin: 0 -30px 5px 0;
}

.solution__box-subject::-webkit-scrollbar {
  width: 0;
}

.solution__box-subject-link {
  white-space: nowrap;
  font-size: 38px;
  line-height: 37px;
  color: #B3B3BA;
  border: 0;
  cursor: pointer;
}

.solution__box-subject-link:not(:last-child) {
  margin-right: 30px;
}

.solution__box-subject-link--active {
  background: -webkit-linear-gradient(90.13deg, #EF8080 0.84%, #F0145A 99.96%);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.solution__box-subject-blur {
  background-image: url(../img/home-page/right-blur.png);
  position: absolute;
  top: -4px;
  right: -30px;
  width: 50px;
  height: 50px;
}

.solution__box-border {
  width: 100%;
  height: 4px;
  background-color: rgba(255, 255, 255, 0.3);
  margin-bottom: 30px;
}

.solution__box-info {
  display: none;
}

.solution__box-info-title {
  font-size: 20px;
  line-height: 24px;
  color: #FFFFFF;
  margin-bottom: 30px;
}

.solution__box-info-text {
  font-weight: 400;
  font-size: 19px;
  line-height: 30px;
  color: #FFFFFF;
}

.consistent-text.visible,
.customer-text.visible,
.innovative-text.visible,
.business-text.visible {
  display: block;
}

@media screen and (min-width: 768px) {
  .solution__box {
    margin-bottom: 60px;
  }
  .solution__box-subject {
    margin: 0 -60px 35px 0;
  }
  .solution__box-subject-blur {
    right: -60px;
  }
  .solution__box-subject-link {
    font-size: 38px;
  }
  .solution__box-border {
    margin-bottom: 60px;
  }
  .solution__box-info {
    display: none;
  }
  .solution__box-info-title {
    font-size: 30px;
    line-height: 37px;
  }
}

@media screen and (min-width: 1024px) {
  .solution__box {
    display: flex;
    justify-content: space-between;
  }
  .solution__box-subject {
    margin: 0 60px 0 0;
    padding-right: 60px;
    border-right: 4px solid rgba(255, 255, 255, 0.3);
    flex-direction: column;
    overflow: inherit;
    align-items: flex-end;
    justify-content: space-around;
  }
  .solution__box-subject-blur {
    display: none;
  }
  .solution__box-subject-link:not(:last-child) {
    margin-right: 0;
  }
  .solution__box-subject-link:last-child {
    margin-right: 0;
  }
  .solution__box-border {
    display: none;
  }
  .solution__box-info {
    margin: 10px 0;
  }
}

@media screen and (min-width: 1440px) {
  .solution__box {
    padding: 0 120px;
  }
}

.service-link {
